import { SandboxContainer  } from '../../../lib/njscore-package/src/web/HTML/SandboxContainer';
export class Solution {
    sandbox: SandboxContainer;
    app: any;
    constructor(id: string) {
      // this.sandbox = new SandboxContainer(id, 'TFJS');  
      this.bind();

      // this.execute('./01_Typescript_Basics');
      // this.execute('./02_Condition_Loop');
      // this.execute('./03_Data_Structure');
      // this.execute('./04_Function_Class');
      // this.execute('./05_Exercise');
      // this.execute('./06_Tensor');
      // this.execute('./07_ModelLinearRegression');
      // this.execute('./08_ModelPrediction');
      // this.execute('./09_Models');
      // this.execute('./10_Model');
      // this.execute('./10_KNN');


    }
    public async execute(path: string) {
      import(`${path}`).then(({ Solution }) => { this.app = new Solution(this.sandbox.divContainer.id)});
    }
    public destroyApp() {
      if (this.app !== undefined) {
        this.app.destroy();
      }
    }
    public bind() {
      this.sandbox.addMenu('Typescript', () => { this.destroyApp();
        this.execute('./01_Typescript_Basics')
      });
      this.sandbox.addMenu('Condition & Loop', () => { this.destroyApp();
        this.execute('./02_Condition_Loop')
      });
      this.sandbox.addMenu('Data Structure', () => { this.destroyApp();
        this.execute('./03_Data_Structure')
      });
      this.sandbox.addMenu('Function & Class', () => { this.destroyApp();
        this.execute('./04_Function_Class')
      });
      this.sandbox.addMenu('Exercise', () => { this.destroyApp();
        this.execute('./05_Exercise')
      });
      this.sandbox.addMenu('Tensor', () => { this.destroyApp();
        this.execute('./06_Tensor')
      });
      this.sandbox.addMenu('LinearRegression', () => { this.destroyApp();
        this.execute('./07_ModelLinearRegression')
      });
      this.sandbox.addMenu('MPG Prediction', () => { this.destroyApp();
        this.execute('./08_MPGPrediction')
      });
      this.sandbox.addMenu('Polynomial Regression', () => { this.destroyApp();
        this.execute('./09_PolynomialRegression')
      });
      this.sandbox.addMenu('KNN', () => { this.destroyApp();
        this.execute('./10_KNN')
      });
      this.sandbox.addMenu('Iris Prediction', () => { this.destroyApp();
        this.execute('./11_IrisPrediction')
      });
    }
    public destroy() {
        this.destroyApp();
        this.sandbox.destroy();
    }
}
